	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Clear HTTP Auth","date":"06 Mar 2004","slug":"/2004-03-06-clear-http-auth/","tags":"Y2004"},"html":"<p>Via <a title=\"Simon Willison: Two handy FireFox extensions\" href=\"http://simon.incutio.com/archive/2004/03/05/handyExtensions\">Simon Willison</a>: <a title=\"Extension Room :: Clear HTTP Auth\" href=\"http://extensionroom.mozdev.org/more-info/clearhttpauth\">Clear HTTP Auth extension</a> for Mozilla and Firefox. This will be a big timesaver for me at work, since I don't really care how my internal web apps look (best viewed with <a title=\"Lynx is a text browser for the World Wide Web\" href=\"http://lynx.browser.org/\">lynx</a>), but I <em>do</em> care that the authentication works.</p>"},"next":{"fields":{"title":"","date":"04 Mar 2004","slug":"/2004-03-04-president-koehler/"}},"previous":{"fields":{"title":"Blog Meet in Hamburg","date":"07 Mar 2004","slug":"/2004-03-07-blog-meet-in-hamburg/"}}},{"node":{"fields":{"title":"","date":"04 Mar 2004","slug":"/2004-03-04-president-koehler/","tags":"Y2004"},"html":"<p>To loosely paraphrase Helmut Kohl, politics is like making sausage. It doesn't matter what goes in, what matters is what comes out. And for the three opposition parties in Germany, who together have a majority to determine the new German <em>Bundespresident</em> in May, the process of grinding out a   common candidate has been very messy indeed. That common candidate is the current head of the International Monetary Fund <a title=\"IMF Chairman Resigns (washingtonpost.com)\" href=\"http://www.washingtonpost.com/wp-dyn/articles/A30514-2004Mar4.html\">Horst Köhler</a>.</p>\n<p>The office of <em>Bundespresident</em> is for the most part ceremonial and non-political, but the election every 5 years of the president by a convention of the federal Bundestag and the state parliments is highly political. This year has been more political than usual, with the rivarly between CDU and CSU, and the current weakness of the FDP. </p>\n<p>Germany has been lucky with its presidents. The first presidents I experienced, von Wiezsacker and Herzog, were eloquent spokesmen for Germany, its history, and its place in the world. Unfortunately, the SPD opted for an active poltician when it had its say in 1999. While Johannes Rau has been a good president, he has not lived up to his predecessors. He is unable to rise far enough above the poltical fray to exert moral authority, the only authority the office affords. </p>\n<p>The first choice of the Union parties, Wolfgang Schäuble, is also an active poltician, and would have had the same weaknesses as Rau. Therefore I am pleased that the selection has gone to a non-politician. I know little about Köhler, except that he served under Kohl, and has been a capable IMF director. Knowledge of global finance can certainly be an asset for a Germany searching for the proper role in the global economy and political order. And now the politcians who chose him can return to their daily business, producing sausage. What matters is what comes out.</p>\n<p>(Coincidentally, sausage-maker Kohl today presented the first volume of his <a title=\"Amazon.de: Bücher: Erinnerungen\" href=\"http://www.amazon.de/exec/obidos/ASIN/3426272180/qid=1078437363/sr=2-1/ref=sr_2_11_1/028-5204065-7104530\">memoirs</a>.)</p>"},"next":{"fields":{"title":"NY Times: Al Qaeda operatives were tracked by their anonymous GSM SIM cards","date":"04 Mar 2004","slug":"/2004-03-04-ny-times-al-qaeda-operatives-were-tracked-by-their-anonymous-gsm-sim-cards/"}},"previous":{"fields":{"title":"Clear HTTP Auth","date":"06 Mar 2004","slug":"/2004-03-06-clear-http-auth/"}}},{"node":{"fields":{"title":"NY Times: Al Qaeda operatives were tracked by their anonymous GSM SIM cards","date":"04 Mar 2004","slug":"/2004-03-04-ny-times-al-qaeda-operatives-were-tracked-by-their-anonymous-gsm-sim-cards/","tags":"Y2004"},"html":"<p><a title=\"Authorities knew their numbers but not their names\" href=\"http://www.nytimes.com/2004/03/04/international/europe/04PHON.html?ex=1393736400&en=71c64fa22f23d30a&ei=5007&partner=USERLAND\">NY Times: Al Qaeda operatives were tracked by their anonymous GSM SIM cards</a></p>"},"next":{"fields":{"title":"Chinese Turnips","date":"04 Mar 2004","slug":"/2004-03-04-chinese-turnips/"}},"previous":{"fields":{"title":"","date":"04 Mar 2004","slug":"/2004-03-04-president-koehler/"}}}],"pathPrefix":"","first":false,"last":false,"index":515,"pageCount":1101}}

